* { z-index: 2; }

body { height: 100vh; }

#page .header-wrapper { z-index: 4; }

.wrapper, .header-wrapper { position: relative; max-width: 1800px; padding: 0 60px; margin: 0 auto; }

@media (max-width: 992px) { .wrapper, .header-wrapper { padding: 0 15px; } }

.site-header { position: absolute; top: 67px; right: 0; left: 0; padding: 0 60px; }

@media (max-width: 992px) { #header-navigations { display: flex; flex-direction: column; } }

@media (max-width: 992px) { .logo { margin-left: 40px; margin-bottom: 30px; } }

@media (max-width: 992px) { .search { margin-left: 40px; margin-bottom: 30px; } }

.menu-list__links li { margin-left: 50px; }

.menu-list__links li a { color: #1a1a1d; font-family: "Nunito", sans-serif; font-size: 14px; text-transform: uppercase; }

.menu__menu-list { display: flex; justify-content: space-between; align-items: center; }

@media (max-width: 992px) { .hamburger { position: absolute; top: 0; right: 14px; width: 80px; height: 60px; cursor: pointer; z-index: 10; transition: opacity .25s ease; }
  .hamburger__item { position: absolute; top: 18px; left: 15px; border: 0; height: 5px; width: 50px; background-color: #f2f2f2; cursor: pointer; }
  .hamburger__item:nth-of-type(2) { top: 33px; }
  .hamburger__item:nth-of-type(3) { top: 48px; }
  .hamburger.active { opacity: 1; z-index: 100; }
  .hamburger.active .hamburger__item { background-color: #58595b; border: 0; height: 5px; width: 55px; border-radius: 0; }
  .hamburger.active .hamburger__item--top { transform: translateY(19px) translateX(-13px) rotate(135deg); background-color: #58595b; transition: .35s ease; }
  .hamburger.active .hamburger__item--middle { opacity: 0; background: #58595b; height: 10px; width: 10px; border-radius: 50%; }
  .hamburger.active .hamburger__item--bottom { transform: translateY(-11px) translateX(-13px) rotate(-135deg); background: #58595b; transition: .35s ease; } }

@media only screen and (max-width: 600px) { .hamburger { position: absolute; width: 60px; height: 60px; }
  .hamburger__item { position: absolute; top: 18px; left: 15px; height: 3px; width: 45px; }
  .hamburger.active .hamburger__item { height: 3px; width: 45px; } }

@media only screen and (max-width: 992px) { .menu-list { position: fixed; padding: 200px 0 0; top: 0; bottom: 0; right: 0; width: 72%; height: 120%; opacity: 0; visibility: hidden; transition: .4s; z-index: 1; transform: translateX(100%); will-change: auto; }
  .menu-list.open { opacity: 1; visibility: visible; height: 120%; background: #fff; box-shadow: 0 0 80px 20px rgba(0, 0, 0, 0.5); transform: translateX(0); display: flex; flex-direction: column-reverse; justify-content: flex-end; align-items: flex-start; }
  .menu-list.open li { margin-bottom: 20px; animation: fadeInRight .5s ease forwards; animation-delay: .35s; }
  .menu-list.open li:nth-of-type(2) { animation-delay: .4s; }
  .menu-list.open li:nth-of-type(3) { animation-delay: .5s; }
  .menu-list.open li:nth-of-type(4) { animation-delay: .6s; }
  .menu-list.open li:nth-of-type(5) { animation-delay: .7s; }
  .menu-list.open li:nth-of-type(6) { animation-delay: .8s; }
  .menu-list.open li:nth-of-type(7) { animation-delay: .9s; }
  .menu-list.open li:nth-of-type(8) { animation-delay: 1s; }
  .menu-list.open li:nth-of-type(9) { animation-delay: 1.1s; }
  .menu-list.open li:nth-of-type(10) { animation-delay: 1.2s; }
  .menu-list__links { display: flex; padding-left: 0; padding-right: 0; margin-top: 0; text-align: center; flex-direction: column; }
  .menu-list__item { display: block; opacity: 0; padding-right: 0; text-align: left; }
  .menu-list__link { display: block; width: 100%; padding: 0 0 0 50px; height: 85px; border-bottom: 0; transition: .4s; background: none; color: #58595b; font: 500 30px/85px "Nunito", sans-serif; }
  .menu-list:hover a { border-bottom: 0; -webkit-tap-highlight-color: rgba(78, 136, 251, 0.6); -webkit-user-select: none; -webkit-touch-callout: none; touch-action: manipulation; }
  @keyframes fadeInRight { 0% { opacity: 0;
      left: 20%; }
    100% { opacity: 1;
      left: 0; } } }

@media only screen and (max-width: 600px) { .menu-list__link { height: 50px; font: 500 20px/50px "Nunito", sans-serif; } }

.contact { padding-bottom: 47px; }

.contact .wrapper { padding: 0; }

.contact-main-container { display: flex; }

@media (max-width: 1086px) { .contact-main-container { flex-wrap: wrap; } }

.contact-location { position: relative; flex-basis: 45%; }

.contact-text-container { padding: 279px 50px 180px 100px; }

.contact-title { position: relative; max-width: 413px; color: #ffffff; font-family: "Poppins", sans-serif; font-size: 101px; }

.contact-title:after { content: ''; position: absolute; left: 130px; bottom: 15px; display: block; width: 76px; height: 7px; background-color: #ffffff; }

.contact-description { padding-top: 71px; color: #ffffff; font-size: 17px; font-family: "Nunito", sans-serif; text-transform: uppercase; }

.contact-info-container { flex-basis: 45%; padding: 270px 0 0 190px; }

.contact-info-container:after { content: ''; position: absolute; right: 0; top: calc((100% - 451px)/2); display: block; width: 241px; height: 451px; background-image: linear-gradient(-135deg, #b4d3d3 0%, #d9e7e7 100%); z-index: -1; }

.contact-info-description { margin-bottom: 135px; color: #1a1a1d; font-family: "Poppins", sans-serif; font-size: 24px; font-weight: normal; }

.contact-info-description::first-letter { font-size: 80px; }

.contact-info-item { display: flex; flex-wrap: wrap; }

.contact-info-data { display: flex; flex-basis: 29%; margin-right: 65px; margin-bottom: 43px; }

.contact-info-data p { margin-bottom: 25px; }

.data-container { margin-left: 35px; }

.data-container p { color: #77777e; font-family: "Nunito", sans-serif; font-weight: normal; }

.dark { position: absolute; height: 100%; width: 100%; z-index: -1; background-color: rgba(0, 0, 0, 0.6); }

.location { display: flex; justify-content: flex-end; flex-basis: 30%; margin-top: 20%; }

.location-text { padding: 50px 133px; background-color: #b4d3d3; color: #ffffff; font-size: 14px; font-family: "Nunito", sans-serif; text-transform: uppercase; border: none; border-radius: 0; }

.location-icon { padding: 45px; border: none; border-radius: 0; background-color: #ffffff; font-size: 30px; }

@media (max-width: 1086px) { .contact-info-container { position: relative; flex-basis: 100%; padding: 270px 15px 0; }
  .contact-location { flex-basis: 100%; } }

@media (max-width: 670px) { .contact-title { font-size: 60px; }
  .contact-title:after { left: unset; right: 0; }
  .contact-text-container { padding: 279px 15px 100px; }
  .location-text { padding: 20px; width: 100%; }
  .location-icon { padding: 20px; } }

@media (max-width: 427px) { .contact-title { max-width: 300px; }
  .contact-info-data { flex-basis: 100%; }
  .contact-info-container { padding: 100px 15px 0; } }

@media (max-width: 1162px) { .location { margin-top: 64%; } }

.site-footer { padding: 0 0 48px; }

.top { display: flex; justify-content: space-between; }

@media (max-width: 670px) { .top { flex-wrap: wrap; justify-content: center; } }

.top-info-container { display: flex; align-items: center; }

.top-info { display: flex; align-items: center; margin-right: 40px; color: #1a1a1d; font-size: 14px; font-family: "Poppins", sans-serif; font-weight: 700; text-transform: uppercase; }

.top-info:before { content: ''; display: block; margin-right: 50px; width: 1px; height: 40px; background-color: #b4d3d3; }

.bottom:before { content: ''; display: block; margin-top: 44px; width: 100%; height: 2px; background-color: #f5f5f5; }

.bottom-container { display: flex; justify-content: space-between; margin-top: 48px; }

.bottom-container .social-container { display: flex; }

.bottom-container .social-container .social-item { margin-left: 40px; margin-bottom: 0; }

.rights { color: #77777e; font-family: "Nunito", sans-serif; font-size: 15px; }

@media (max-width: 460px) { .bottom-container { flex-wrap: wrap-reverse; }
  .rights { text-align: center; margin-top: 30px; } }
